'use strict';
import React from 'react';
import _ from 'lodash';
import {bindMethods, openDataPage} from 'service';
import {connect} from 'react-redux';
import {openCopyModal} from '../react/actions/coupon-actions.js';

import s from 'styles/CouponManager/Aside.scss';

export default class Aside extends React.Component {
	constructor(props) {
		super(props);
		this.state = {};
		bindMethods(this, ['openModal', 'printCoupons', 'getActiveSetCoupons']);
	}
	componentDidMount() {}
	openModal(){
		this.props.openCopyModal();
	}
	printCoupons() {
		const couponset = this.getActiveSetCoupons();
		const name = this.props.selectedCoupon.name;
		openDataPage(couponset, name);
	}
	getActiveSetCoupons() {
		const selectedCoupons = [];
		this
			.props
			.coupons
			.items
			.forEach((el, key) => {
				if(el.name === this.props.selectedCoupon.name){
					selectedCoupons.push(el);
				}
			});
		return selectedCoupons;
	}
	render() {
		return (
			<aside className={s.Aside}>
				<h2>Please select coupon you want to manage</h2>
				<div><button className="btn btn-primary btn-large" onClick={this.openModal}><i className="icon-gallery"></i>Copy coupon</button></div>
				<br/>
				<div>
					<button
						className="btn btn-primary btn-large"
						onClick={this.printCoupons}
						>
						<i className="icon-print" />
						Print coupon
					</button>
				</div>
			</aside>
		);
	}
}
function mapStateToProps(state) {
    return {
			modal: state.couponReducer.modal,
			selectedCoupon: state.couponReducer.selectedCoupon,
			coupons: state.couponReducer.coupons,
		}
}
export default connect(mapStateToProps, {openCopyModal})(Aside);



