export function getCouponsFromBase() {
    const response = axios.get(`https://app.ecwid.com/api/v3/${storeId}/discount_coupons?token=${accessToken}`);
    return {
        type: types.GET_COUPONS_FROM_BASE,
        payload: response
    }
}
